}

.stage-cube-cont {
  width: 50%;
  height: 100%;
  top: 0;
  padding-top: 18%;
  margin-left: 0;
  position: absolute;
  right: 0;
  overflow: hidden;

  @media screen and (max-width: 768px) {
    width: 75%;
    padding-top: 25%;
  }

  @media screen and (max-width: 480px) {
    width: 100%;
    padding-top: 160%;
    max-height: 35%;
  }
}

.cubespinner {
  animation-name: spincube;
  animation-timing-function: ease-in-out;
  animation-iteration-count: infinite;
  animation-duration: 12s;
  transform-style: preserve-3d;
  transform-origin: 100px 100px 0;
  margin-left: calc(50% - 100px);

  @media screen and (max-width: 768px) {
    transform-origin: 75px 75px 0;
    margin-left: calc(50% - 75px);
  }

  @media screen and (max-width: 480px) {
    transform-origin: 50px 50px 0;
    margin-left: calc(50% - 50px);
    
  }

  div {
    position: absolute;
    width: 200px;
    height: 200px;
    border: 1px solid #ccc;
    background: rgba(255, 255, 255, 0.4);
    text-align: center;
    font-size: 100px;
    display: flex;
    justify-content: center;
    align-items: center;
    box-shadow: 0 0 20px 0px lightyellow;

    @media screen and (max-width: 768px) {
      width: 150px;
      height: 150px;
      font-size: 75px;
    }

    @media screen and (max-width: 480px) {
      width: 100px;
      height: 100px;
      font-size: 50px;
    }
  }

  .face1 {
    transform: translateZ(100px);
    color: #dd0031;

    @media screen and (max-width: 768px) {
      transform: translateZ(75px);
    }

    @media screen and (max-width: 480px) {
      transform: translateZ(50px);
    }
  }

  .face2 {
    transform: rotateY(90deg) translateZ(100px);
    color: #f06529;

    @media screen and (max-width: 768px) {
      transform: rotateY(90deg) translateZ(75px);
    }

    @media screen and (max-width: 480px) {
      transform: rotateY(90deg) translateZ(50px);
    }
  }

  .face3 {
    transform: rotateY(90deg) rotateX(90deg) translateZ(100px);
    color: #28a4d9;

    @media screen and (max-width: 768px) {
      transform: rotateY(90deg) rotateX(90deg) translateZ(75px);
    }

    @media screen and (max-width: 480px) {
      transform: rotateY(90deg) rotateX(90deg) translateZ(50px);
    }
  }

  .face4 {
    transform: rotateY(180deg) rotateZ(90deg) translateZ(100px);
    color: #5ed4f4;

    @media screen and (max-width: 768px) {
      transform: rotateY(180deg) rotateZ(90deg) translateZ(75px);
    }

    @media screen and (max-width: 480px) {
      transform: rotateY(180deg) rotateZ(90deg) translateZ(50px);
    }
  }

  .face5 {
    transform: rotateY(-90deg) rotateZ(90deg) translateZ(100px);
    color: #efd81d;

    @media screen and (max-width: 768px) {
      transform: rotateY(-90deg) rotateZ(90deg) translateZ(75px);
    }

    @media screen and (max-width: 480px) {
      transform: rotateY(-90deg) rotateZ(90deg) translateZ(50px);
    }
  }

  .face6 {
    transform: rotateX(-90deg) translateZ(100px);
    color: #ec4d28;

    @media screen and (max-width: 768px) {
      transform: rotateX(-90deg) translateZ(75px);
    }

    @media screen and (max-width: 480px) {
      transform: rotateX(-90deg) translateZ(50px);
    }
  }
}

.loading {
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: -1;
  opacity: 0.02;
}

.clear:before,
.clear:after {
  content: ' ';
  display: table;
}

.clear:after {
  clear: both;
}

.clear {
  *zoom: 1;
  clear: both;
}

@keyframes animStar {
  from {
    transform: translateY(0px);
  }

  to {
    transform: translateY(-2000px);
  }
}

@keyframes spincube {

  from,
  to {
    transform: rotateX(0deg) rotateY(0deg) rotateZ(0deg);
  }

  16% {
    transform: rotateY(-90deg) rotateZ(90deg);
  }

  33% {
    transform: rotateY(-90deg) rotateX(90deg);
  }

  50% {
    transform: rotateY(-180deg) rotateZ(90deg);
  }

  66% {
    transform: rotateY(-270deg) rotateX(90deg);
  }

  83% {
    transform: rotateX(90deg);
  }
}
